<template>
    <div>
        <v-header>
        </v-header>

        <v-content :type="isEdit ? 'none load-lazy':'form load-lazy'">
            <div class="picture-box tran-all-3" v-for="item in items">
                <div class="opt-box">
                    <p class="title">{{item.name}}</p>
                    <span class="color-main" v-if="!item.isEdit && !isEdit" @click="edit(item,$index)">编辑</span>
                    <span :class="['color-main',selected.length > 0 ? '':'vis-hide']" v-if="item.isEdit"
                          @click="del">删除</span>
                    <span class="color-main" style="margin-right:.15rem;" v-if="item.isEdit" @click="cancel">取消</span>
                    <span class="choose color-9" v-if="item.isEdit && item.list.length > selected.length"
                          @click="chooseAll">全选</span>
                    <span class="choose color-9" v-if="item.isEdit && item.list.length == selected.length"
                          @click="chooseNone">取消全选</span>
                </div>
                <div :class="['pic-view',item.id == cpItem.id ? 'on-edit':'']">
                    <div v-for="pic in item.list"
                         data-original="{{pic.imgPath | img-host}}"
                         style="width:{{picSize}}px;height:{{picSize}}px;background-color:#f2f2f2;"
                         :class="['lazyload picture',$index % 4 == 0 ? 'first':'',selected.indexOf(pic.id)>-1?'selected':'']"
                         @click="choose(pic.id,item,$index)">
                    </div>
                    <div style="width:{{picSize}}px;height:{{picSize}}px;"
                         v-show="!item.isEdit" :class="['add-pic',item.list.length % 4 == 0 ? 'first':'']">
                        <input type="file" accept="image/jpeg,image/png,image/jpg,image/gif"
                               class="upload opa-hide z-index-2" pid="{{item.id}}" pindex="{{$index}}">
                        <span class="img add"></span>
                    </div>
                </div>
            </div>
            <div class="none_bg need-login" v-if="items.length == 0">
                <span class="img bg03"></span>
                <p class="remark">
                    您还没有发布照片
                </p>
            </div>
        </v-content>

        <v-footer-btn v-show="!isEdit">
            <input type="button" value="新建技能秀" class="input max ok" v-link="{name:'show-add'}"/>
        </v-footer-btn>

        <v-pic-view></v-pic-view>
    </div>
</template>
<script>
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'
    import VFooterBtn from '../../components/FooterBtn'
    import VPicView from '../../components/PicView'
    import $ from 'jquery'
    import {url} from '../../const'
    import {toast} from '../../assets/js/toast'
    require('../../assets/js/lazyload')
    require('../../assets/js/lrz.all.bundle');

    export default{
        route: {
            data({from, to}){
                this.cancel();
                this.viewed = [];
                this.$broadcast('reset-pic-view');
                return this.$http.get(url.skillsShow).then(
                    ({data:{data, success}}) => {
                        if (success) {
                            for (let i in data) {
                                data[i].isEdit = false;
                            }
                            this.items = data;
                            this.lazyload();
                        } else {
                            toast.alert('网络错误');
                        }
                    })
            }
        },
        ready(){
            let vm = this;
            let size = $(window).width() - 20 - 5 * 3;
            vm.picSize = size / 4;
            $(".content").on('click', '.upload', function () {
                $(this).val('');
            });
            $(".content").on('change', '.upload', function () {
                if (!this.files || !this.files.length) return;
                let files = this.files[0];
                let id = $(this).attr('pid');
                let index = $(this).attr('pindex');
                id = parseInt(id);
                index = parseInt(index);
                console.log('id=', id, ',index=', index);
                vm.addItem = {id: id, index: index};
                if (!/image\/\w+/.test(files.type)) {
                    toast.alert("图片格式不正确，请选择正确格式的图片文件！");
                    return false;
                } else {
                    var fileReader = new FileReader();
                    fileReader.onload = function (e) {
                        let ua = navigator.userAgent.toLowerCase();
                        if (/android/.test(ua)) {
                            vm.loadAndroid(files);
                        } else {
                            lrz(files, {width: 667})
                                .then(function (rst) {
                                    toast.loadEnd();
                                    var base64 = rst.base64;
                                    vm.addPic(base64);
                                })
                                .catch(function (err) {
                                    toast.alert("图片处理失败");
                                });
                        }
                    };
                    fileReader.onerror = function (e) {
                        toast.alert("图片加载失败");
                    };
                    fileReader.readAsDataURL(files);
                    toast.loading();
                }
            });
        },
        data(){
            return {
                isEdit: false,
                selected: [],//子id合集
                items: [],
                picSize: 0,
                editIndex: -1,
                cpItem: {id: 0, isEdit: false},
                addItem: {id: 0, index: -1},
                viewed: []//已查看的
            }
        },
        methods: {
            lazyload(){
                this.$nextTick(function () {
                    $(".lazyload").lazyload({
                        effect: "fadeIn",
                        threshold: 5,
                        container: $('.content.load-lazy')
                    });
                });
            },
            addPic(picture){
                let id = this.addItem.id;
                this.$http.get(url.addSkill, {
                    picture: picture,
                    type: this.addItem.id
                }).then(
                    ({data:{data, success, resultMsg}}) => {
                        if (success) {
                            toast.alert('新增照片成功');
                            if (data) {
                                data.isEdit = false;
                                this.items.splice(this.addItem.index, 1, data);
                                this.lazyload();
                            } else {
                                this.items.splice(this.addItem.index, 1);
                            }
                            this.removeViewed(id);
                        } else {
                            toast.alert(resultMsg);
                        }
                    });
            },
            loadAndroid(file){
                let vm = this;
                setTimeout(function () {
                    lrz(file, {width: 667})
                        .then(function (rst) {
                            vm.addPic(rst.base64);
                        });
                }, 100);
            },
            chooseAll(){
                let selected = [];
                let list = this.cpItem.list;
                for (let i = 0; i < list.length; i++) {
                    selected.push(list[i].id);
                }
                this.selected = selected;
            },
            chooseNone(){
                this.selected = [];
            },
            edit(item, index){
                if (this.editIndex > -1) {
                    toast.alert('当前已存在编辑中内容');
                    return;
                }
                this.isEdit = true;
                item.isEdit = true;
                this.editIndex = index;
                this.cpItem = item;
            },
            cancel(){
                this.isEdit = false;
                this.cpItem.isEdit = false;
                this.editIndex = -1;
                this.selected = [];
                this.cpItem = {id: 0, isEdit: false};
            },
            del(){
                if (this.selected.length == 0) {
                    toast.alert('请先选择需要删除的图片');
                    return;
                }
                let id = this.cpItem.id;
                this.$http.get(url.deletePhoto, {
                    phId: this.selected,
                    type: this.cpItem.id
                }).then(
                    ({data:{data, success, resultMsg}}) => {
                        if (success) {
                            toast.alert('删除照片成功');
                            if (data) {
                                data.isEdit = false;
                                this.cpItem = data;
                                this.items.splice(this.editIndex, 1, this.cpItem);
                                this.lazyload();
                            } else {
                                this.items.splice(this.editIndex, 1);
                            }
                            this.cancel();
                            this.removeViewed(id);
                        } else {
                            toast.alert(resultMsg);
                        }
                    });
            },
            removeViewed(id){
                let index = this.viewed.indexOf(id);
                if (index > -1) {
                    this.viewed.splice(index, 1);
                }
            },
            choose(id, item, index){
                if (this.cpItem.id == item.id) {//编辑
                    let index = this.selected.indexOf(id);
                    if (index > -1) {
                        this.selected.splice(index, 1);
                    } else {
                        this.selected.push(id);
                    }
                } else {//查看
                    let list = item.list;
                    // console.log('list:',JSON.stringify(list));
                    let items = [];
                    let link = '';
                    for (let i = 0; i < list.length; i++) {
                        link = list[i].imgPath;
                        link = link.split('.').join('big.');
                        items.push({
                            id: list[i].id,
                            link: link
                        });
                    }
                    let review = this.viewed.indexOf(item.id) > -1;
                    let data = {items: items, index: index, review: review};
                    this.$broadcast('pic-view', data);
                    this.viewed.push(item.id);
                }
            }
        },
        components: {VHeader, VContent, VPicView, VFooterBtn}
    }

</script>

<style scoped>
    .picture-box {
        overflow: hidden;
        background-color: #fff;
        margin-bottom: .1rem;
    }

    .picture-box .opt-box {
        height: .45rem;
        line-height: .45rem;
        padding: 0 .1rem;
        border-bottom: .01rem solid #f2f2f2;
    }

    .opt-box .title {
        font-size: .16rem;
        color: #333;
        float: left;
    }

    .opt-box span {
        float: right;
        font-size: .12rem;
    }

    .opt-box .choose {
        margin-right: .45rem;
    }

    .pic-view {
        padding: .05rem .1rem .15rem;
        overflow: hidden;
    }

    .pic-view > div {
        position: relative;
        margin-left: .05rem;
        margin-top: .1rem;
        float: left;
        background-size: 100% 100%;
    }

    .pic-view > div.first {
        margin-left: 0;
    }

    .pic-view > div:last-child {
        border: .01rem solid #f2f2f2;
    }

    .pic-view.on-edit .picture:after {
        content: '';
        position: absolute;
        width: .18rem;
        height: .18rem;
        right: .05rem;
        top: .05rem;
        background-size: .18rem;
        border: .01rem solid #fff;
        border-radius: 50%;
        background-color: rgba(155, 155, 155, 0.65);
    }

    .pic-view.on-edit .picture.selected:after {
        opacity: 1;
        background-image: url(../../assets/img/icon/selected.png);
        border: none;
    }

    .add-pic .upload {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .img.add {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -.125rem;
        margin-top: -.125rem;
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        background-image: url(../../assets/img/icon/add_i01.png);
    }

    .img.bg03 {
        background-image: url(../../assets/img/bg/null_icon03.png);
    }
</style>



